import React, { useRef, useEffect } from 'react';
import Confetti from 'confetti-react';
import styled, { keyframes } from 'styled-components';
import { MdClose } from 'react-icons/md'
import Typed from 'typed.js'

const AniBounceIn = keyframes`
 from,
  20%,
  40%,
  60%,
  80%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }

  20% {
    transform: scale3d(1.1, 1.1, 1.1);
  }

  40% {
    transform: scale3d(0.9, 0.9, 0.9);
  }

  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03);
  }

  80% {
    transform: scale3d(0.97, 0.97, 0.97);
  }

  to {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
`;
const StyledPopup = styled.section`
z-index: 99999;
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background-color:rgba(2,2,2,.8);
  .tip{
      box-sizing: border-box;
      width: 5rem;
      z-index: 9;
      position: absolute;
      top:20%;
      left: 50%;
      margin-left: -2.5rem ;
      padding:.4rem .3rem;
      font-size: .3rem;
      background-color: #fff;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      align-items: center;
      animation: ${AniBounceIn} 1s both;
      .title{
          font-size: .3rem;
          font-weight: 800;
      }
      .content{
        margin:.2rem 0;
          color: #333;
          font-size: .2rem;
          padding:.3rem 0;
          word-break: break-all;
          width: 100%;
          p{
              margin-bottom: .14rem;
              line-height: 1.5;
          }
          strong{
              font-weight: 800;
              padding:0 .05rem;
              color: #000;
          }
      }
      .ps{
          font-size: .12rem;
          color: #ccc;
          align-self: flex-start;
      }
      .close{
          cursor: pointer;
          position: absolute;
          left:50%;
          bottom:-.8rem;
          transform: translate3d(-50%,0,0);
          width:.3rem;
          height: .3rem;
          border-radius: 50%;
          border:1px solid #eee;
          padding:.05rem;
      }
  }
`;
export default function Celebrate({ closeDan, dan = "" }) {
  const el = useRef(null);
  // Create reference to store the Typed instance itself
  const typed = useRef(null);
  useEffect(() => {
    if (dan) {
      // elRef refers to the <span> rendered below
      typed.current = new Typed(el.current, {
        strings: [
          `<p></p>
          <p><strong>${dan}</strong><strong></strong>:<strong>yanggc_2013</strong></p>
          <p>10</p>`,
        ],
        typeSpeed: 30,
        showCursor: false
      });
    }

    return () => {
      if (dan) {
        // Make sure to destroy Typed instance during cleanup
        // to prevent memory leaks
        typed.current.destroy();
      }
    }
  }, [dan]);
  return (
    <StyledPopup>
      <Confetti gravity={0.2} numberOfPieces={300} />
      <div className="tip">
        <h3 className="title"></h3>
        <div className="content" ref={el}>
        </div>
        <div className="ps">* </div>
        <div className="close" onClick={closeDan}>
          <MdClose color="#fff" />
        </div>
      </div>
    </StyledPopup>
  )
}
